<template>
    <div class="form">
        <p>
            <el-input placeholder="请输入标题" v-model="title"></el-input>
        </p>
        <p>
            <el-input placeholder="请输入URL" v-model="url"></el-input>
        </p>
        <p>
            <el-select v-model="type" placeholder="请选择类型" value="" class="select">
                <el-option
                        v-for="item in options"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                </el-option>
            </el-select>
        </p>
        <p>
            <el-checkbox v-model="login_require">需要登陆才能访问?</el-checkbox>
        </p>
        <p>
            <el-input type="textarea" placeholder="请输入简介(可空)" v-model="description"
                      :autosize="{ minRows: 2}"></el-input>
        </p>
        <p class="title">Send 发送的数据
            <el-button icon="el-icon-plus" type="primary" @click="add_send" round
                       v-if="!send.length"
                       class="s_add"></el-button>
        </p>
        <p v-for="(item,index) in send" :key="index" class="add_li">
            <el-input placeholder="请输入参数名" v-model="item.name" class="add"></el-input>
            <el-input placeholder="请输入类型" v-model="item.type" class="add"></el-input>
            <el-button icon="el-icon-minus" type="danger" @click="remove_send(index)" round></el-button>
            <el-button icon="el-icon-plus" type="primary" @click="add_send" round
                       v-if="index === send.length - 1"></el-button>
        </p>
        <p class="title">Receive 接受的数据
            <el-button icon="el-icon-plus" type="primary" @click="add_receive" round
                       v-if="!receive.length"
                       class="s_add"></el-button>
        </p>
        <p v-for="(item,index) in receive" :key="'rec' + index" class="add_li">
            <el-input placeholder="请输入参数名" v-model="item.name" class="add"></el-input>
            <el-input placeholder="请输入类型" v-model="item.type" class="add"></el-input>
            <el-button icon="el-icon-minus" type="danger" @click="remove_receive(index)" round></el-button>
            <el-button icon="el-icon-plus" type="primary" @click="add_receive" round
                       v-if="index === receive.length - 1"></el-button>
        </p>
        <p>
            <el-button class="submit" type="primary" @click="submit">提交</el-button>
        </p>
    </div>
</template>

<script>
    export default {
        name: "AddApi",
        props: ["api", "data"],
        data() {
            return {
                title: "",
                url: "",
                type: "",
                login_require: false,
                description: "",
                send: [{name: "", type: ""}],
                receive: [{name: "status", type: "bool"}, {name: "error", type: "str"}],
                options: [{
                    value: 'GET',
                    label: 'Get'
                }, {
                    value: 'POST',
                    label: 'Post'
                }, {
                    value: 'DELETE',
                    label: 'Delete'
                }, {
                    value: 'PATCH',
                    label: 'Patch'
                }, {
                    value: 'PUT',
                    label: 'Put'
                }],
            };
        },
        mounted() {
            if (this.data) {
                this.title = this.data.title;
                this.url = this.data.url;
                this.type = this.data.type;
                this.login_require = this.data.login_require;
                this.description = this.data.description;
                this.send = this.data.send;
                this.receive = this.data.receive;
            }
        },
        methods: {
            add_send() {
                let obj = {name: "", type: ""};
                this.send.push(obj);
            },
            remove_send(index) {
                this.send.splice(index, 1);
            },
            add_receive() {
                let obj = {name: "", type: ""};
                this.receive.push(obj);
            },
            remove_receive(index) {
                this.receive.splice(index, 1);
            },
            submit() {
                if (!(this.title && this.url && this.type)) {
                    this.$message.error("有必填字段未填");
                    return;
                }
                for (let i = 0; i < this.send.length; i++) {
                    if (!(this.send[i].name && this.send[i].type)) {
                        this.$message.error("发送字段不完整");
                        return;
                    }
                }
                for (let i = 0; i < this.receive.length; i++) {
                    if (!(this.receive[i].name && this.receive[i].type)) {
                        this.$message.error("接受字段不完整");
                        return;
                    }
                }
                let obj = {
                    title: this.title,
                    url: this.url,
                    type: this.type,
                    description: this.description,
                    login_require: this.login_require,
                    send: this.send,
                    receive: this.receive
                };
                if (this.data) {
                    obj["id"] = this.data.id;
                }
                this.axios.post(this.$bus.base_url + this.api, obj).then(resp => {
                    if (resp.data.status) {
                        this.$message.success("成功");
                        location.reload();
                    } else {
                        this.$message.error("失败: " + resp.data.error)
                    }
                })
            }
        }
    }
</script>

<style scoped>
    .form {
        width: 90%;
        box-shadow: 0 0 10px lightgray;
        padding: 10px 5%;
        margin: 10px 0;
        text-align: left;
    }

    .select {
        width: 100%;
    }

    .add {
        width: 33%;
        margin-right: 10px;
    }

    .add_li {
        text-align: left;
    }

    .submit {
        width: 100%;
        margin-top: 20px;
    }

    .s_add {
        float: right;
    }

    .title {
        height: 40px;
        font-size: 18px;
    }
</style>