<template>
    <div class="card">
        <el-input placeholder="请输入查询内容" v-model="q">
            <el-button icon="el-icon-search" slot="append" @click="getData"></el-button>
        </el-input>
        <el-button class="btn" @click="add" >{{hint}}</el-button>
        <AddApi v-if="status" api="/add/"></AddApi>
        <ApiItem v-for="item in data" :data="item" :key="item.url"></ApiItem>
    </div>
</template>

<script>
    import AddApi from "./AddApi"
    import ApiItem from "./ApiItem"

    export default {
        name: "ApiList",
        components: {
            AddApi,
            ApiItem
        },
        data() {
            return {
                q: "",
                data: [],
                hint: "添加API",
                status: false
            }
        },
        mounted() {
            this.getData()
        },
        methods: {
            getData() {
                let url = this.$bus.base_url + "/apis/";
                if (this.q)
                    url += "?q=" + this.q;
                this.axios.get(url)
                    .then((resp) => {
                        this.data = resp.data
                    })
            },
            add(){
                if (this.status){
                    this.hint = "添加API";
                    this.status = false;
                } else {
                    this.hint = "取消";
                    this.status = true
                }
            }
        }
    }
</script>

<style scoped>
    .card {
        width: 60%;
        margin: 30px auto;
    }
    .btn{
        width: 100%;
        margin-top: 10px;
    }
</style>